h2 {
    color: #fff;
    font-size: 2rem;
    font-family: 'Merriweather';
    font-weight: 400;
    line-height: 1.5;
}

.iamfrontend {
    color: #d8ac9c;
}

.me {
    border-radius: 30px;
    width: 80%;
}

.me-container {
    margin-left: 4rem;
}

@media only screen and (max-width: 600px) {
    .description {
        padding-left: 30px;
        padding-right: 30px;
    }
    
    .me {
        margin-top: 4rem;
    }
}

.me-description {
    color: #f2edd7;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 400;
    font-size: 1.25rem;
    margin-top: 2.5rem;
}