@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&family=Source+Sans+Pro:wght@200;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kurale&display=swap);
* {
    margin: 0;
    padding: 0;
    
}

body {
    background-color: #1b1717;
}

.head-bar {
    background-color: #1b1717;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0);
    color: #f2edd7;
    padding: 1rem;
}

.nav-link {
    font-size: 1rem;
    color: #f2edd7;
}

.nav-link:hover {
    color: #eac8af;
}

.enlace {
    font-family: "Raleway", sans-serif;
    font-weight: 500;
    margin-right: 1rem;
    margin-left: 1rem;
}
.filled-box {
  font-size: 60%;
}

.ilustration {
  display: block;
  margin: 0 auto;
  width: 80%;
}

.home-section {
  margin-top: 5rem;
}

.homeTitles {
  text-align: center;
  color: #eeebdd;
}

h1 {
  font-family: "Merriweather";
  font-size: 90px;
  font-weight: 700;
}

.whoiam {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
}

.programmer {
  color: #d8e3e7;
}

@media only screen and (max-width: 600px) {
  h1 {
    font-size: 45px;
  }

  h2 {
    font-size: 1rem;
  }
}
.book-collection {
    margin-top: 4rem;
    text-align: center;
}

.book-img {
    border-radius: 5px;
    width: 200px;
    height: 300px;
}

.book-info {
    margin-top: 1.5rem;
    font-family: "Source Sans Pro", sans-serif;
    font-weight: 400;
    font-size: 18px;
}

.book-title, .book-author {
    color: #f2edd7;
}

.book-title {
    font-family: 'Kurale', serif;
}

.reading {
    margin-top: 10rem;
}

.reading-title {
    text-align: right;
}

@media only screen and (max-width: 600px) {
    .reading-title {
        text-align: center;
    }

    .book-item {
        margin-bottom: 4rem;
    }
}
h2 {
    color: #fff;
    font-size: 2rem;
    font-family: 'Merriweather';
    font-weight: 400;
    line-height: 1.5;
}

.iamfrontend {
    color: #d8ac9c;
}

.me {
    border-radius: 30px;
    width: 80%;
}

.me-container {
    margin-left: 4rem;
}

@media only screen and (max-width: 600px) {
    .description {
        padding-left: 30px;
        padding-right: 30px;
    }
    
    .me {
        margin-top: 4rem;
    }
}

.me-description {
    color: #f2edd7;
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 400;
    font-size: 1.25rem;
    margin-top: 2.5rem;
}
.contact {
    margin: 15rem 0;
    text-align: center;
}

.contact-p {
    color: #f2edd7;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.4rem;
    font-weight: 400;
}

.contact-me {
    color: #d8ac9c;
}

.contact-me:hover {
    color: #eac8af;
}

.contact-title {
    font-size: 50px;
    font-weight: 900;
}
h2, .skill {
    color: #f2edd7;
}

.ejs {
    font-weight: 700;  
}

.express {
    font-weight: 200;
}

spam, .fab, .fas {
    color: #d8ac9c;
}

.skills-container {
    margin-top: 10rem;
}

.skill-row {
    margin-bottom: 1rem;
}

.skill {
    font-size: 1.2rem;
    font-family: "Source Sans Pro", sans-serif;
    font-weight: 400;
}

.skills {
    margin-top: 3rem;
}
.about-container {
    margin-top: 10rem;
}


.projects-title {
    font-weight: 700;
    font-size: 3rem;
}

.text-container, .working-gif {
    margin: 5rem auto;
}


.working-gif {
    margin-bottom: 8rem;
}

.working-gif-img {
    width: 40%;
}

@media only screen and (max-width: 600px) {
    .projects-title {
        font-size: 2.5rem;
    }

    .working-gif-img {
        width: 85%;
    }

    .text-container {
        margin: 10rem 0;
    }
}
.artwork-title {
    color: #1b2021;
    font-weight: 900;
}

.artwork {
    background-color: #d8ac9c;
    border-radius: 15px;
    max-width: 500px;
    padding: 3rem;
    margin-top: 3rem;
}

.artwork-text {
    font-family: "Source Sans Pro", sans-serif;
    font-size: 1.2rem;
    font-weight: 400;
}

.insta {
    color: #1b2021;
    font-weight: 700;
}

.insta:hover {
    color: #ff8882;
}

@media only screen and (max-width: 600px) {
    .artwork {
        max-width: 300px;
    }
}
footer {
  background-color: #1b1717;
  color: #f2edd7;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 18px;
  font-weight: 400;
  text-align: center;
  padding: 2rem, auto;
}

@media only screen and (max-width: 600px) {
  footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 1rem;
    padding-bottom: 0.5rem;
  }
}

