.artwork-title {
    color: #1b2021;
    font-weight: 900;
}

.artwork {
    background-color: #d8ac9c;
    border-radius: 15px;
    max-width: 500px;
    padding: 3rem;
    margin-top: 3rem;
}

.artwork-text {
    font-family: "Source Sans Pro", sans-serif;
    font-size: 1.2rem;
    font-weight: 400;
}

.insta {
    color: #1b2021;
    font-weight: 700;
}

.insta:hover {
    color: #ff8882;
}

@media only screen and (max-width: 600px) {
    .artwork {
        max-width: 300px;
    }
}