footer {
  background-color: #1b1717;
  color: #f2edd7;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 18px;
  font-weight: 400;
  text-align: center;
  padding: 2rem, auto;
}

@media only screen and (max-width: 600px) {
  footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 1rem;
    padding-bottom: 0.5rem;
  }
}
