.projects-title {
    font-weight: 700;
    font-size: 3rem;
}

.text-container, .working-gif {
    margin: 5rem auto;
}


.working-gif {
    margin-bottom: 8rem;
}

.working-gif-img {
    width: 40%;
}

@media only screen and (max-width: 600px) {
    .projects-title {
        font-size: 2.5rem;
    }

    .working-gif-img {
        width: 85%;
    }

    .text-container {
        margin: 10rem 0;
    }
}