.book-collection {
    margin-top: 4rem;
    text-align: center;
}

.book-img {
    border-radius: 5px;
    width: 200px;
    height: 300px;
}

.book-info {
    margin-top: 1.5rem;
    font-family: "Source Sans Pro", sans-serif;
    font-weight: 400;
    font-size: 18px;
}

.book-title, .book-author {
    color: #f2edd7;
}

.book-title {
    font-family: 'Kurale', serif;
}

.reading {
    margin-top: 10rem;
}

.reading-title {
    text-align: right;
}

@media only screen and (max-width: 600px) {
    .reading-title {
        text-align: center;
    }

    .book-item {
        margin-bottom: 4rem;
    }
}